.products__wrap {
  margin-top: 123px;
  text-align: center;

  @include tablet {
    margin-top: 108px;
  }
  @include desktop {
    margin-top: 166px;
  }
}

.products__sub-title {
  @include font(null, 20px, 1.2, null);
  text-transform: uppercase;
  margin-bottom: 40px;

  @include tablet {
    @include font(null, 22px, 1.23, null);
  }
  @include desktop {
    @include font(null, 30px, null, null);
    margin-bottom: 65px;
  }
}

.products__text {
  font-size: 14px;

  @include desktop {
    font-size: 16px;
  }
}
.products__btn {
  width: 60px;
  height: 24px;
  align-self: flex-end;

  cursor: pointer;
  margin-right: auto;
  margin-left: auto;
}
